import { Cell, Notify } from "@taroify/core"
import { ArrowRight } from "@taroify/~icons/src"
import * as React from "react"
import { ReactNode, useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicNotify() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell title="" clickable rightIcon={<ArrowRight />} onClick={() => setOpen(true)} />
      <Notify open={open} onClose={setOpen} children="" />
    </>
  )
}

function CustomNotify() {
  const [options, setOptions] = useState<{
    open?: boolean
    duration?: number
    children?: ReactNode
  }>({})

  return (
    <>
      <Cell
        title=""
        clickable
        rightIcon={<ArrowRight />}
        onClick={() => {
          setOptions({
            open: true,
            children: "",
          })
        }}
      />
      <Cell
        title=""
        clickable
        rightIcon={<ArrowRight />}
        onClick={() =>
          setOptions({
            open: true,
            duration: 1000,
            children: "",
          })
        }
      />
      <Notify
        open={options.open}
        style={{
          color: "#ad0000",
          background: "#ffe1e1",
        }}
        duration={options.duration}
        children={options.children}
        onClose={() => setOptions({ open: false })}
      />
    </>
  )
}

function NotifyWithPresetColors() {
  return (
    <>
      <Notify id="notify" />
      <Cell
        title=""
        clickable
        rightIcon={<ArrowRight />}
        onClick={() => Notify.open({ color: "primary", message: "" })}
      />
      <Cell
        title=""
        clickable
        rightIcon={<ArrowRight />}
        onClick={() => Notify.open({ color: "success", message: "" })}
      />
      <Cell
        title=""
        clickable
        rightIcon={<ArrowRight />}
        onClick={() => Notify.open({ color: "danger", message: "" })}
      />
      <Cell
        title=""
        clickable
        rightIcon={<ArrowRight />}
        onClick={() => Notify.open({ color: "warning", message: "" })}
      />
    </>
  )
}

export default function NotifyDemo() {
  return (
    <Page title="Notify " className="notify-demo">
      <Block variant="card" title="">
        <BasicNotify />
      </Block>
      <Block variant="card" title="">
        <NotifyWithPresetColors />
      </Block>
      <Block variant="card" title="">
        <CustomNotify />
      </Block>
    </Page>
  )
}
